html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-size: 1.8rem;
  font-family: "Barlow", sans-serif;
}

.container {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Barlow", sans-serif;
  margin: 0;
  line-height: 1.2;
}

h1, h2 {
  font-weight: 700;
}

h1 {
  font-size: 2rem;
}

p {
  font-family: "Fraunces", serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

.header {
  min-height: 50rem;
  padding: 5rem 0;
  background-image: url("../img/mobile/image-header.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: bottom center;
}
@media (min-width: 768px) {
  .header {
    background-image: url("../img/desktop/image-header.jpg");
    background-repeat: no-repeat;
    background-size: cover;
  }
}
.header__navbar {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__logo {
  width: 15rem;
}
.header__btn {
  background-color: transparent;
  border: 0;
}
@media (min-width: 768px) {
  .header__btn {
    display: none;
  }
}
.header__btn:hover {
  cursor: pointer;
}

.hero__heading {
  text-align: center;
  font-family: "Fraunces", serif;
  text-transform: uppercase;
  color: white;
  font-size: 5rem;
  padding: 10rem 0;
}
@media (min-width: 768px) {
  .hero__heading {
    padding: 12rem 0 29.9rem 0;
  }
}
.hero__heading span {
  display: block;
}
@media (min-width: 768px) {
  .hero__heading span {
    display: inline-block;
  }
}
.hero__heading::after {
  content: "";
  display: block;
  justify-content: center;
  width: 4rem;
  height: auto;
  background-image: url("../img/icon-arrow-down.svg");
  background-position: center center;
  background-repeat: no-repeat;
  margin: 0 auto;
  padding-top: 25rem;
  transition-property: transform;
  transition-duration: 0.3s;
}
@media (min-width: 768px) {
  .hero__heading::after {
    padding-top: 30rem;
  }
}

.navigation {
  display: none;
  align-items: center;
  gap: 3rem;
}
@media (max-width: 768px) {
  .navigation.show {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    position: absolute;
    top: 6rem;
    left: 1%;
    width: 98%;
    margin: 0 auto;
    padding: 4rem 3rem;
    min-height: 40rem;
    background-color: white;
  }
}
@media (min-width: 768px) {
  .navigation {
    display: flex;
    flex-direction: row;
  }
}
.navigation__link {
  color: #5a636c;
  text-decoration: none;
  font-weight: 400;
  font-family: "Fraunces", serif;
  font-size: 2.1rem;
}
@media (min-width: 768px) {
  .navigation__link {
    color: white;
    font-size: unset;
  }
}
.navigation__link--submit {
  background-color: #fad400;
  padding: 2rem 3rem;
  text-transform: uppercase;
  border-radius: 4rem;
  font-weight: 700;
}
@media (min-width: 768px) {
  .navigation__link--submit {
    background-color: white;
    color: #23303e;
  }
}

@media (min-width: 768px) {
  .topics__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 0rem;
    grid-auto-flow: dense;
  }
}

.topic {
  display: flex;
  flex-direction: column;
  padding: 5rem 0;
}
@media (max-width: 768px) {
  .topic {
    min-height: 40rem;
  }
}
@media (min-width: 768px) {
  .topic {
    padding: 9.5rem 0;
  }
}
.topic:nth-child(1) {
  grid-column: 2/3;
  background-image: url("../img/mobile/image-transform.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
}
@media (min-width: 768px) {
  .topic:nth-child(1) {
    background-image: url("../img/desktop/image-transform.jpg");
    background-repeat: no-repeat;
    background-size: cover;
  }
}
.topic:nth-child(3) {
  background-image: url("../img/mobile/image-stand-out.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
}
@media (min-width: 768px) {
  .topic:nth-child(3) {
    background-image: url("../img/desktop/image-stand-out.jpg");
    background-repeat: no-repeat;
    background-size: cover;
  }
}
.topic__info {
  padding: 4rem;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}
@media (min-width: 768px) {
  .topic__info {
    text-align: left;
    padding: 7rem;
  }
}
@media (min-width: 1024px) {
  .topic__info {
    padding: 10rem;
  }
}
.topic__heading {
  font-family: "Fraunces", serif;
  font-weight: 900;
  font-size: 3rem;
  color: #23303e;
}
@media (min-width: 768px) {
  .topic__heading {
    font-size: 4rem;
  }
}
@media (min-width: 1024px) {
  .topic__heading {
    font-size: 5rem;
  }
}
.topic__text {
  font-family: "Barlow", sans-serif;
  color: #5a636c;
  padding: 0.5rem 0 1.5rem 0;
}
@media (min-width: 768px) {
  .topic__text {
    font-size: 1.7rem;
  }
}
.topic__link {
  font-family: "Fraunces", serif;
  font-weight: 900;
  color: #23303e;
  text-decoration: none;
  text-transform: uppercase;
  text-align: center;
  width: 15rem;
  margin: 0 auto;
  display: inline-block;
}
@media (min-width: 768px) {
  .topic__link {
    margin: 0;
    font-size: 1.8rem;
  }
}
.topic__link--1::after {
  content: "";
  display: block;
  height: 0.7rem;
  background-color: #fff09e;
  margin-top: -0.7rem;
  border-radius: 10rem;
}
.topic__link--2::after {
  content: "";
  display: block;
  height: 0.7rem;
  background-color: #ffd2cc;
  margin-top: -0.7rem;
  border-radius: 10rem;
}

@media (min-width: 768px) {
  .jobs__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 0;
  }
}

.job {
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}
.job:nth-child(1) {
  background-image: url("../img/mobile/image-graphic-design.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  color: #25564b;
}
@media (min-width: 768px) {
  .job:nth-child(1) {
    background-image: url("../img/desktop/image-graphic-design.jpg");
    background-repeat: no-repeat;
    background-size: cover;
  }
}
.job:nth-child(2) {
  background-image: url("../img/mobile/image-photography.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  color: #19536b;
}
@media (min-width: 768px) {
  .job:nth-child(2) {
    background-image: url("../img/desktop/image-photography.jpg");
    background-repeat: no-repeat;
    background-size: cover;
  }
}
.job__info {
  padding-top: 43rem;
  padding-bottom: 7rem;
}
@media (min-width: 768px) {
  .job__info {
    max-width: 40rem;
    margin: 0 auto;
  }
}
.job__heading {
  font-family: "Fraunces", serif;
  font-weight: 900;
}
@media (min-width: 768px) {
  .job__heading {
    font-size: 3rem;
  }
}
.job__text {
  font-size: 1.5rem;
}
@media (min-width: 768px) {
  .job__text {
    font-size: 1.7rem;
  }
}

.testimonials {
  background-color: white;
  text-align: center;
  padding: 9rem 0 5rem 0;
}
@media (min-width: 768px) {
  .testimonials {
    padding: 15rem 0;
  }
}
.testimonials__heading {
  text-transform: uppercase;
  font-family: "Fraunces", serif;
  color: #a7abae;
}
.testimonials__grid {
  padding-top: 2rem;
}
@media (min-width: 768px) {
  .testimonials__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  }
}

.testimonial {
  padding: 4rem;
}
.testimonial__image {
  display: inline-block;
  overflow: hidden;
  border-radius: 50%;
  margin: 0 auto;
}
.testimonial__info {
  max-width: 31rem;
  min-height: 22rem;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
@media (min-width: 768px) {
  .testimonial__info {
    max-width: 32rem;
  }
}
.testimonial__quote {
  color: #23303e;
  font-family: "Barlow", sans-serif;
  font-size: 1.65rem;
}
.testimonial__author {
  font-family: "Fraunces", serif;
  font-weight: 900;
  color: #23303e;
  margin-bottom: 0.8rem;
  font-size: 1.8rem;
}
.testimonial__job {
  margin: 0;
  font-family: "Barlow", sans-serif;
  color: #818498;
}

.gallery__grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}
@media (min-width: 768px) {
  .gallery__grid {
    grid-template-columns: repeat(4, 1fr);
  }
}

.picture {
  aspect-ratio: 1/1;
  background-position: bottom center;
}
.picture:nth-child(1) {
  background-image: url("../img/mobile/image-gallery-milkbottles.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}
@media (min-width: 768px) {
  .picture:nth-child(1) {
    background-image: url("../img/desktop/image-gallery-milkbottles.jpg");
    background-repeat: no-repeat;
    background-size: cover;
  }
}
.picture:nth-child(2) {
  background-image: url("../img/mobile/image-gallery-orange.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}
@media (min-width: 768px) {
  .picture:nth-child(2) {
    background-image: url("../img/desktop/image-gallery-orange.jpg");
    background-repeat: no-repeat;
    background-size: cover;
  }
}
.picture:nth-child(3) {
  background-image: url("../img/mobile/image-gallery-cone.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}
@media (min-width: 768px) {
  .picture:nth-child(3) {
    background-image: url("../img/desktop/image-gallery-cone.jpg");
    background-repeat: no-repeat;
    background-size: cover;
  }
}
.picture:nth-child(4) {
  background-image: url("../img/mobile/image-gallery-sugar-cubes.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}
@media (min-width: 768px) {
  .picture:nth-child(4) {
    background-image: url("../img/desktop/image-gallery-sugarcubes.jpg");
    background-repeat: no-repeat;
    background-size: cover;
  }
}

.footer {
  background-color: #458c7e;
  padding-top: 5rem;
}
.footer__logo {
  filter: invert(75%) sepia(70%) saturate(300%) hue-rotate(100deg) brightness(70%) contrast(100%);
  width: 30%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .footer__logo {
    width: 20%;
  }
}
.footer__navigation {
  display: flex;
  justify-content: space-between;
  max-width: 31rem;
  margin: 0 auto;
  padding: 3rem 0;
}
@media (min-width: 768px) {
  .footer__navigation {
    max-width: 40rem;
  }
}
.footer__link {
  color: #25564b;
  text-decoration: none;
  font-size: 2rem;
}
.footer__social {
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  padding: 8rem 0;
  max-width: 20rem;
}
.footer__social-link img {
  height: 3rem;
  filter: invert(35%) sepia(60%) saturate(300%) hue-rotate(100deg) brightness(50%) contrast(110%);
}